.container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
}

.glow {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 600px;
    height: 600px;
    background: radial-gradient(circle, rgba(240, 71, 71, 0.1) 0%, transparent 60%);
    pointer-events: none;
}

.main {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 40px 24px;
}

.content {
    text-align: center;
    max-width: 500px;
    animation: fadeIn 0.6s ease-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.iconWrapper {
    margin-bottom: 32px;
}

.icon {
    width: 80px;
    height: 80px;
    color: #f04747;
}

.title {
    font-size: 42px;
    font-weight: 700;
    margin-bottom: 16px;
    color: #f04747;
}

.description {
    font-size: 17px;
    line-height: 1.7;
    color: #a0a0b0;
    margin-bottom: 40px;
}

.actions {
    display: flex;
    gap: 16px;
    justify-content: center;
    margin-bottom: 40px;
}

.btnSecondary {
    display: inline-flex;
    align-items: center;
    padding: 14px 28px;
    background: rgba(255, 255, 255, 0.05);
    color: #ffffff;
    font-size: 15px;
    font-weight: 600;
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    cursor: pointer;
    transition: all 0.25s ease;
}

.btnSecondary:hover {
    background: rgba(255, 255, 255, 0.1);
    border-color: rgba(255, 255, 255, 0.2);
}

.btnDanger {
    display: inline-flex;
    align-items: center;
    padding: 14px 28px;
    background: #f04747;
    color: white;
    font-size: 15px;
    font-weight: 600;
    border: none;
    border-radius: 12px;
    cursor: pointer;
    transition: all 0.25s ease;
}

.btnDanger:hover {
    background: #d63b3b;
    transform: translateY(-2px);
}

.info {
    display: flex;
    align-items: flex-start;
    gap: 12px;
    padding: 16px 20px;
    background: rgba(255, 255, 255, 0.03);
    border: 1px solid rgba(255, 255, 255, 0.08);
    border-radius: 12px;
    text-align: left;
}

.info svg {
    width: 20px;
    height: 20px;
    flex-shrink: 0;
    color: #6b6b7b;
    margin-top: 2px;
}

.info span {
    font-size: 14px;
    color: #6b6b7b;
    line-height: 1.5;
}